.simplebar-track.simplebar-horizontal {
	width: 100%;
	height: 20px;
	background: $color-hint-of-red;
	&::after {
		content: url(../img/sprite/icon-triangle-left.svg);
		position: absolute;
		left: 0;
		right: auto;
		display: inline-block;
		width: 20px;
		height: 20px;
	}
	&::before {
		content: url(../img/sprite/icon-triangle-right.svg);
		position: absolute;
		left: auto;
		right: 0;
		display: inline-block;
		width: 20px;
		height: 20px;
	}
}
.simplebar-track.simplebar-horizontal .simplebar-scrollbar {
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	height: 100%;
	width: 100%;
}

.simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
	height: 100%;
	background: $color-fantasy;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	opacity: 1;
	border-radius: unset;
}

.finished-products {
	.simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
		width: calc(100% - 40px);;
		left: 20px;
	}
}

.package {
	.simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
		width: calc(100% - 40px);;
		left: 20px;
	}
}

.calculations {
	.simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
		width: calc(100% - 47px);
		left: 23.5px;
	}
}

.orders-and-shipments {
	.simplebar-track.simplebar-horizontal .simplebar-scrollbar::before {
		width: calc(100% - 40px);;
		left: 20px;
		@include vp--425 {
			min-width: 6px;
			left: 50%;
			transform: translateX(-50%);
		}
	}
}
